
import { onMounted, ref } from 'vue'
import Portrait from './../Portrait/Portrait.vue'
import picture from './max.jpg'
import * as content from './content'

export default {
  name: 'bio',
  components: {
    Portrait,
  },
  setup() {
    const konamicode = [
      {
        key: 38,
        active: false,
      },
      {
        key: 38,
        active: false,
      },
      {
        key: 40,
        active: false,
      },
      {
        key: 40,
        active: false,
      },
      {
        key: 37,
        active: false,
      },
      {
        key: 39,
        active: false,
      },
      {
        key: 37,
        active: false,
      },
      {
        key: 39,
        active: false,
      },
      {
        key: 66,
        active: false,
      },
      {
        key: 65,
        active: false,
      },
    ]
    const entries = ref(konamicode)
    const visibility = ref(0)

    const show = () => {
      visibility.value = 1

      setTimeout(() => {
        entries.value.forEach(v => (v.active = false))
        visibility.value = 0
      }, 3000)
    }

    const releaseKey = key => {
      // Find first unactive entries
      const next = entries.value.find(e => !e.active)

      // No next action required for now
      if (!next) {
        return
      }

      // Active entry or desactive all
      if (next && key === next.key) {
        next.active = true
      } else {
        entries.value.forEach(e => {
          e.active = false
        })
      }

      // Check if all entries are active
      if (!entries.value.find(e => !e.active)) {
        show()
      }
    }

    const keyupHandler = e => {
      releaseKey(e.keyCode)
    }

    const isClickable = i => {
      const next = entries.value.findIndex(e => !e.active)

      return i === next
    }

    onMounted(() => {
      window.addEventListener('keyup', keyupHandler)
    })

    return {
      content,
      isClickable,
      entries,
      releaseKey,
      visibility,
      picture,
    }
  },
}
