
import { ref } from 'vue'
import * as theme from '@/store/theme'
import * as content from './content'

export default {
  name: 'projects',
  setup() {
    const currentPicture = ref('')

    const onMouseEnter = project => {
      theme.dark()
      currentPicture.value = project.picture
    }

    const onMouseLeave = () => {
      theme.light()
    }

    return {
      content,
      currentPicture,
      onMouseEnter,
      onMouseLeave,
      theme,
    }
  },
}
